import * as THREE from 'three'
import { BlockType, BLOCK_PROPERTIES } from '../blocks/Block'

// 
const CHUNK_SIZE = 16
const CHUNK_HEIGHT = 32
const RENDER_DISTANCE = 3

interface ChunkData {
  blocks: Uint8Array
  mesh?: THREE.InstancedMesh
  dirty: boolean
}

export class World {
  private scene: THREE.Scene
  private chunks: Map<string, ChunkData> = new Map()
  private geometry: THREE.BoxGeometry
  private materials: Map<BlockType, THREE.MeshStandardMaterial> = new Map()
  private lastPlayerChunk: THREE.Vector3 = new THREE.Vector3(Infinity, 0, Infinity)
  private blockCount: number = 0

  constructor(scene: THREE.Scene) {
    this.scene = scene
    this.geometry = new THREE.BoxGeometry(1, 1, 1)
    
    // 
    this.initMaterials()
    
    // 
    this.generateInitialChunks()
  }

  private initMaterials(): void {
    for (const [type, props] of Object.entries(BLOCK_PROPERTIES)) {
      const material = new THREE.MeshStandardMaterial({
        color: props.color,
        roughness: 0.8,
        metalness: 0.1
      })
      this.materials.set(parseInt(type), material)
    }
  }

  private getChunkKey(cx: number, cz: number): string {
    return `${cx},${cz}`
  }

  private generateInitialChunks(): void {
    for (let x = -RENDER_DISTANCE; x <= RENDER_DISTANCE; x++) {
      for (let z = -RENDER_DISTANCE; z <= RENDER_DISTANCE; z++) {
        this.generateChunk(x, z)
      }
    }
    this.updateMeshes()
  }

  private generateChunk(cx: number, cz: number): void {
    const key = this.getChunkKey(cx, cz)
    if (this.chunks.has(key)) return

    const blocks = new Uint8Array(CHUNK_SIZE * CHUNK_HEIGHT * CHUNK_SIZE)
    
    // 
    for (let x = 0; x < CHUNK_SIZE; x++) {
      for (let z = 0; z < CHUNK_SIZE; z++) {
        const worldX = cx * CHUNK_SIZE + x
        const worldZ = cz * CHUNK_SIZE + z
        
        // 
        const height = this.getTerrainHeight(worldX, worldZ)
        
        for (let y = 0; y < CHUNK_HEIGHT; y++) {
          let blockType = BlockType.AIR
          
          if (y < height - 3) {
            blockType = BlockType.STONE
          } else if (y < height) {
            blockType = BlockType.DIRT
          } else if (y === height) {
            blockType = BlockType.GRASS
            
            // 
            if (Math.random() < 0.02 && y < CHUNK_HEIGHT - 5) {
              this.generateTree(blocks, x, y + 1, z)
            }
          }
          
          if (blockType !== BlockType.AIR) {
            this.setBlockInChunk(blocks, x, y, z, blockType)
            this.blockCount++
          }
        }
      }
    }
    
    this.chunks.set(key, { blocks, dirty: true })
  }

  private getTerrainHeight(x: number, z: number): number {
    // 
    const scale1 = 0.02
    const scale2 = 0.05
    const noise1 = Math.sin(x * scale1) * Math.cos(z * scale1)
    const noise2 = Math.sin(x * scale2 + 1) * Math.cos(z * scale2 + 1) * 0.5
    return Math.floor(10 + (noise1 + noise2) * 5)
  }

  private generateTree(blocks: Uint8Array, x: number, y: number, z: number): void {
    // 
    for (let i = 0; i < 4; i++) {
      if (y + i < CHUNK_HEIGHT) {
        this.setBlockInChunk(blocks, x, y + i, z, BlockType.WOOD)
      }
    }
    
    // 
    for (let dx = -2; dx <= 2; dx++) {
      for (let dz = -2; dz <= 2; dz++) {
        for (let dy = 0; dy <= 1; dy++) {
          if (Math.abs(dx) + Math.abs(dz) + dy < 3) {
            const ly = y + 3 + dy
            if (ly < CHUNK_HEIGHT && this.getBlockInChunk(blocks, x + dx, ly, z + dz) === BlockType.AIR) {
              this.setBlockInChunk(blocks, x + dx, ly, z + dz, BlockType.LEAVES)
            }
          }
        }
      }
    }
  }

  private getBlockIndex(x: number, y: number, z: number): number {
    return y * CHUNK_SIZE * CHUNK_SIZE + z * CHUNK_SIZE + x
  }

  private getBlockInChunk(blocks: Uint8Array, x: number, y: number, z: number): BlockType {
    if (x < 0 || x >= CHUNK_SIZE || y < 0 || y >= CHUNK_HEIGHT || z < 0 || z >= CHUNK_SIZE) {
      return BlockType.AIR
    }
    return blocks[this.getBlockIndex(x, y, z)]
  }

  private setBlockInChunk(blocks: Uint8Array, x: number, y: number, z: number, type: BlockType): void {
    if (x >= 0 && x < CHUNK_SIZE && y >= 0 && y < CHUNK_HEIGHT && z >= 0 && z < CHUNK_SIZE) {
      blocks[this.getBlockIndex(x, y, z)] = type
    }
  }

  private updateMeshes(): void {
    for (const [key, chunk] of this.chunks) {
      if (chunk.dirty) {
        this.updateChunkMesh(key, chunk)
        chunk.dirty = false
      }
    }
  }

  private updateChunkMesh(key: string, chunk: ChunkData): void {
    // 
    if (chunk.mesh) {
      this.scene.remove(chunk.mesh)
      chunk.mesh.dispose()
    }

    const [cx, cz] = key.split(',').map(Number)
    const instances: Map<BlockType, THREE.Matrix4[]> = new Map()

    // 
    for (let x = 0; x < CHUNK_SIZE; x++) {
      for (let y = 0; y < CHUNK_HEIGHT; y++) {
        for (let z = 0; z < CHUNK_SIZE; z++) {
          const blockType = this.getBlockInChunk(chunk.blocks, x, y, z)
          if (blockType !== BlockType.AIR && this.isBlockVisible(chunk, x, y, z)) {
            if (!instances.has(blockType)) {
              instances.set(blockType, [])
            }
            const matrix = new THREE.Matrix4()
            matrix.setPosition(cx * CHUNK_SIZE + x, y, cz * CHUNK_SIZE + z)
            instances.get(blockType)!.push(matrix)
          }
        }
      }
    }

    // 
    for (const [blockType, matrices] of instances) {
      const material = this.materials.get(blockType)!
      const mesh = new THREE.InstancedMesh(this.geometry, material, matrices.length)
      mesh.castShadow = true
      mesh.receiveShadow = true

      for (let i = 0; i < matrices.length; i++) {
        mesh.setMatrixAt(i, matrices[i])
      }
      mesh.instanceMatrix.needsUpdate = true
      this.scene.add(mesh)
      chunk.mesh = mesh
    }
  }

  private isBlockVisible(chunk: ChunkData, x: number, y: number, z: number): boolean {
    // 
    return (
      this.getBlockInChunk(chunk.blocks, x + 1, y, z) === BlockType.AIR ||
      this.getBlockInChunk(chunk.blocks, x - 1, y, z) === BlockType.AIR ||
      this.getBlockInChunk(chunk.blocks, x, y + 1, z) === BlockType.AIR ||
      this.getBlockInChunk(chunk.blocks, x, y - 1, z) === BlockType.AIR ||
      this.getBlockInChunk(chunk.blocks, x, y, z + 1) === BlockType.AIR ||
      this.getBlockInChunk(chunk.blocks, x, y, z - 1) === BlockType.AIR
    )
  }

  getBlock(x: number, y: number, z: number): BlockType {
    const cx = Math.floor(x / CHUNK_SIZE)
    const cz = Math.floor(z / CHUNK_SIZE)
    const key = this.getChunkKey(cx, cz)
    const chunk = this.chunks.get(key)
    
    if (!chunk) return BlockType.AIR
    
    const localX = ((x % CHUNK_SIZE) + CHUNK_SIZE) % CHUNK_SIZE
    const localZ = ((z % CHUNK_SIZE) + CHUNK_SIZE) % CHUNK_SIZE
    return this.getBlockInChunk(chunk.blocks, localX, y, localZ)
  }

  setBlock(x: number, y: number, z: number, type: BlockType): void {
    const cx = Math.floor(x / CHUNK_SIZE)
    const cz = Math.floor(z / CHUNK_SIZE)
    const key = this.getChunkKey(cx, cz)
    const chunk = this.chunks.get(key)
    
    if (!chunk) return
    
    const localX = ((x % CHUNK_SIZE) + CHUNK_SIZE) % CHUNK_SIZE
    const localZ = ((z % CHUNK_SIZE) + CHUNK_SIZE) % CHUNK_SIZE
    
    const oldType = this.getBlockInChunk(chunk.blocks, localX, y, localZ)
    this.setBlockInChunk(chunk.blocks, localX, y, localZ, type)
    chunk.dirty = true
    
    if (oldType === BlockType.AIR && type !== BlockType.AIR) {
      this.blockCount++
    } else if (oldType !== BlockType.AIR && type === BlockType.AIR) {
      this.blockCount--
    }
    
    this.updateChunkMesh(key, chunk)
  }

  update(playerPos: THREE.Vector3): void {
    const pcx = Math.floor(playerPos.x / CHUNK_SIZE)
    const pcz = Math.floor(playerPos.z / CHUNK_SIZE)
    
    if (pcx !== this.lastPlayerChunk.x || pcz !== this.lastPlayerChunk.z) {
      this.lastPlayerChunk.set(pcx, 0, pcz)
      
      // 
      for (let x = pcx - RENDER_DISTANCE; x <= pcx + RENDER_DISTANCE; x++) {
        for (let z = pcz - RENDER_DISTANCE; z <= pcz + RENDER_DISTANCE; z++) {
          this.generateChunk(x, z)
        }
      }
      
      this.updateMeshes()
    }
  }

  getBlockCount(): number {
    return this.blockCount
  }
}