import * as THREE from 'three'
import { NetworkPlayerData } from '../network/NetworkManager'

// 
export class RemotePlayer {
  public id: string
  public name: string
  public color: string
  private mesh!: THREE.Group
  private scene: THREE.Scene
  private targetPosition: THREE.Vector3
  private currentPosition: THREE.Vector3
  private targetRotation: { pitch: number; yaw: number }
  private nameLabel: HTMLDivElement | null = null

  constructor(scene: THREE.Scene, data: NetworkPlayerData) {
    this.id = data.id
    this.name = data.name
    this.color = data.color
    this.scene = scene
    this.targetPosition = new THREE.Vector3(data.position.x, data.position.y, data.position.z)
    this.currentPosition = this.targetPosition.clone()
    this.targetRotation = data.rotation

    this.createMesh()
    this.createNameLabel()
  }

  private createMesh(): void {
    this.mesh = new THREE.Group()

    //  ()
    const bodyGeometry = new THREE.BoxGeometry(0.6, 0.9, 0.3)
    const bodyMaterial = new THREE.MeshStandardMaterial({ color: this.color })
    const body = new THREE.Mesh(bodyGeometry, bodyMaterial)
    body.position.y = 0.45
    body.castShadow = true
    this.mesh.add(body)

    // 
    const headGeometry = new THREE.BoxGeometry(0.4, 0.4, 0.4)
    const headMaterial = new THREE.MeshStandardMaterial({ color: this.color })
    const head = new THREE.Mesh(headGeometry, headMaterial)
    head.position.y = 1.1
    head.castShadow = true
    this.mesh.add(head)

    // 
    const armGeometry = new THREE.BoxGeometry(0.2, 0.7, 0.2)
    const armMaterial = new THREE.MeshStandardMaterial({ color: this.color })
    
    const leftArm = new THREE.Mesh(armGeometry, armMaterial)
    leftArm.position.set(-0.45, 0.6, 0)
    leftArm.castShadow = true
    this.mesh.add(leftArm)

    const rightArm = new THREE.Mesh(armGeometry, armMaterial)
    rightArm.position.set(0.45, 0.6, 0)
    rightArm.castShadow = true
    this.mesh.add(rightArm)

    // 
    const legGeometry = new THREE.BoxGeometry(0.25, 0.75, 0.25)
    const legMaterial = new THREE.MeshStandardMaterial({ color: 0x333333 })
    
    const leftLeg = new THREE.Mesh(legGeometry, legMaterial)
    leftLeg.position.set(-0.15, -0.375, 0)
    leftLeg.castShadow = true
    this.mesh.add(leftLeg)

    const rightLeg = new THREE.Mesh(legGeometry, legMaterial)
    rightLeg.position.set(0.15, -0.375, 0)
    rightLeg.castShadow = true
    this.mesh.add(rightLeg)

    this.mesh.position.copy(this.currentPosition)
    this.scene.add(this.mesh)
  }

  private createNameLabel(): void {
    this.nameLabel = document.createElement('div')
    this.nameLabel.className = 'player-name-label'
    this.nameLabel.textContent = this.name
    this.nameLabel.style.cssText = `
      position: absolute;
      color: ${this.color};
      font-size: 12px;
      font-weight: bold;
      text-shadow: 1px 1px 0 #000;
      pointer-events: none;
      transform: translate(-50%, -100%);
      white-space: nowrap;
    `
    document.body.appendChild(this.nameLabel)
  }

  updatePosition(position: { x: number; y: number; z: number }): void {
    this.targetPosition.set(position.x, position.y, position.z)
  }

  updateRotation(rotation: { pitch: number; yaw: number }): void {
    this.targetRotation = rotation
  }

  update(deltaTime: number, camera: THREE.Camera): void {
    // 
    const lerpFactor = Math.min(1, deltaTime * 10)
    this.currentPosition.lerp(this.targetPosition, lerpFactor)
    this.mesh.position.copy(this.currentPosition)

    //  (Yyaw)
    this.mesh.rotation.y = -this.targetRotation.yaw + Math.PI

    // 
    if (this.nameLabel) {
      const screenPos = this.currentPosition.clone()
      screenPos.y += 1.8 // 
      screenPos.project(camera)

      const x = (screenPos.x * 0.5 + 0.5) * window.innerWidth
      const y = (-(screenPos.y * 0.5) + 0.5) * window.innerHeight

      if (screenPos.z < 1) {
        this.nameLabel.style.display = 'block'
        this.nameLabel.style.left = `${x}px`
        this.nameLabel.style.top = `${y}px`
      } else {
        this.nameLabel.style.display = 'none'
      }
    }

    // 
    const walkSpeed = 8
    const walkCycle = Date.now() * 0.005 * walkSpeed
    const isMoving = this.currentPosition.distanceTo(this.targetPosition) > 0.01

    if (isMoving) {
      this.mesh.children[2].rotation.x = Math.sin(walkCycle) * 0.5 // 
      this.mesh.children[3].rotation.x = Math.sin(walkCycle + Math.PI) * 0.5 // 
      this.mesh.children[4].rotation.x = Math.sin(walkCycle + Math.PI) * 0.5 // 
      this.mesh.children[5].rotation.x = Math.sin(walkCycle) * 0.5 // 
    } else {
      // 
      this.mesh.children[2].rotation.x = 0
      this.mesh.children[3].rotation.x = 0
      this.mesh.children[4].rotation.x = 0
      this.mesh.children[5].rotation.x = 0
    }
  }

  destroy(): void {
    this.scene.remove(this.mesh)
    if (this.nameLabel) {
      document.body.removeChild(this.nameLabel)
    }
  }
}