import { BlockType } from '../blocks/Block'

// 
export enum MessageType {
  PLAYER_JOIN = 'player_join',
  PLAYER_LEAVE = 'player_leave',
  PLAYER_MOVE = 'player_move',
  PLAYER_ROTATE = 'player_rotate',
  BLOCK_BREAK = 'block_break',
  BLOCK_PLACE = 'block_place',
  WORLD_SYNC = 'world_sync',
  PLAYER_LIST = 'player_list',
  CHAT = 'chat',
  PING = 'ping',
  PONG = 'pong'
}

// 
export interface GameMessage {
  type: MessageType
  playerId?: string
  data?: any
  timestamp: number
}

// 
export interface NetworkPlayerData {
  id: string
  name: string
  position: { x: number; y: number; z: number }
  rotation: { pitch: number; yaw: number }
  color: string
}

// 
export class NetworkManager {
  private ws: WebSocket | null = null
  private playerId: string | null = null
  private playerName: string = ''
  private playerColor: string = '#ffffff'
  private isConnected: boolean = false
  private reconnectAttempts: number = 0
  private maxReconnectAttempts: number = 5
  private reconnectDelay: number = 3000
  
  // 
  public onPlayerJoin: ((player: NetworkPlayerData) => void) | null = null
  public onPlayerLeave: ((playerId: string) => void) | null = null
  public onPlayerMove: ((playerId: string, position: { x: number; y: number; z: number }) => void) | null = null
  public onPlayerRotate: ((playerId: string, rotation: { pitch: number; yaw: number }) => void) | null = null
  public onBlockBreak: ((x: number, y: number, z: number, playerId: string) => void) | null = null
  public onBlockPlace: ((x: number, y: number, z: number, blockType: BlockType, playerId: string) => void) | null = null
  public onWorldSync: ((blocks: any[]) => void) | null = null
  public onConnect: (() => void) | null = null
  public onDisconnect: (() => void) | null = null
  public onChat: ((playerId: string, message: string) => void) | null = null

  connect(serverUrl: string = 'ws://localhost:8080'): void {
    try {
      this.ws = new WebSocket(serverUrl)
      
      this.ws.onopen = () => {
        console.log(' ')
        this.isConnected = true
        this.reconnectAttempts = 0
        this.onConnect?.()
        this.showNotification('', 'success')
      }
      
      this.ws.onmessage = (event) => {
        try {
          const message: GameMessage = JSON.parse(event.data)
          this.handleMessage(message)
        } catch (error) {
          console.error(':', error)
        }
      }
      
      this.ws.onclose = () => {
        console.log(' ')
        this.isConnected = false
        this.onDisconnect?.()
        this.showNotification('', 'error')
        this.attemptReconnect(serverUrl)
      }
      
      this.ws.onerror = (error) => {
        console.error('WebSocket:', error)
        this.showNotification('', 'error')
      }
    } catch (error) {
      console.error(':', error)
      this.attemptReconnect(serverUrl)
    }
  }

  private attemptReconnect(serverUrl: string): void {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++
      console.log(` ${this.reconnectAttempts}...`)
      setTimeout(() => this.connect(serverUrl), this.reconnectDelay)
    } else {
      console.error('')
      this.showNotification('', 'error')
    }
  }

  private handleMessage(message: GameMessage): void {
    switch (message.type) {
      case MessageType.PLAYER_JOIN:
        if (message.data) {
          // 
          if (message.data.playerId && !this.playerId) {
            this.playerId = message.data.playerId
            this.playerName = message.data.playerName
            this.playerColor = message.data.playerColor
            console.log(` ID: ${this.playerId}, : ${this.playerName}`)
            this.updatePlayerInfo()
          }
          // 
          else if (message.data.id) {
            console.log(` : ${message.data.name}`)
            this.onPlayerJoin?.(message.data)
            this.showNotification(`${message.data.name} `, 'info')
          }
        }
        break
        
      case MessageType.PLAYER_LEAVE:
        if (message.playerId) {
          console.log(` : ${message.playerId}`)
          this.onPlayerLeave?.(message.playerId)
        }
        break
        
      case MessageType.PLAYER_MOVE:
        if (message.playerId && message.data?.position) {
          this.onPlayerMove?.(message.playerId, message.data.position)
        }
        break
        
      case MessageType.PLAYER_ROTATE:
        if (message.playerId && message.data?.rotation) {
          this.onPlayerRotate?.(message.playerId, message.data.rotation)
        }
        break
        
      case MessageType.BLOCK_BREAK:
        if (message.data) {
          this.onBlockBreak?.(
            message.data.x,
            message.data.y,
            message.data.z,
            message.playerId || 'unknown'
          )
        }
        break
        
      case MessageType.BLOCK_PLACE:
        if (message.data) {
          this.onBlockPlace?.(
            message.data.x,
            message.data.y,
            message.data.z,
            message.data.blockType,
            message.playerId || 'unknown'
          )
        }
        break
        
      case MessageType.WORLD_SYNC:
        if (message.data?.blocks) {
          console.log(` : ${message.data.blocks.length} `)
          this.onWorldSync?.(message.data.blocks)
        }
        break
        
      case MessageType.CHAT:
        if (message.playerId && message.data?.message) {
          this.onChat?.(message.playerId, message.data.message)
          this.showChatMessage(message.playerId, message.data.message)
        }
        break
        
      case MessageType.PING:
        this.send({ type: MessageType.PONG })
        break
    }
  }

  send(message: Omit<GameMessage, 'timestamp'>): void {
    if (this.ws?.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify({
        ...message,
        timestamp: Date.now()
      }))
    }
  }

  // 
  sendPlayerMove(position: { x: number; y: number; z: number }): void {
    this.send({
      type: MessageType.PLAYER_MOVE,
      data: { position }
    })
  }

  // 
  sendPlayerRotate(rotation: { pitch: number; yaw: number }): void {
    this.send({
      type: MessageType.PLAYER_ROTATE,
      data: { rotation }
    })
  }

  // 
  sendBlockBreak(x: number, y: number, z: number): void {
    this.send({
      type: MessageType.BLOCK_BREAK,
      data: { x, y, z }
    })
  }

  // 
  sendBlockPlace(x: number, y: number, z: number, blockType: BlockType): void {
    this.send({
      type: MessageType.BLOCK_PLACE,
      data: { x, y, z, blockType }
    })
  }

  // 
  sendChat(message: string): void {
    this.send({
      type: MessageType.CHAT,
      data: { message }
    })
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close()
      this.ws = null
    }
  }

  isOnline(): boolean {
    return this.isConnected
  }

  getPlayerId(): string | null {
    return this.playerId
  }

  getPlayerName(): string {
    return this.playerName
  }

  getPlayerColor(): string {
    return this.playerColor
  }

  private updatePlayerInfo(): void {
    const infoEl = document.getElementById('player-info')
    if (infoEl) {
      infoEl.textContent = `: ${this.playerName}`
      infoEl.style.color = this.playerColor
    }
  }

  private showNotification(message: string, type: 'success' | 'error' | 'info'): void {
    // 
    console.log(`[${type}] ${message}`)
  }

  private showChatMessage(playerId: string, message: string): void {
    const chatContainer = document.getElementById('chat-messages')
    if (chatContainer) {
      const msgEl = document.createElement('div')
      msgEl.className = 'chat-message'
      msgEl.innerHTML = `<span class="chat-player">${playerId.slice(0, 8)}:</span> <span>${this.escapeHtml(message)}</span>`
      chatContainer.appendChild(msgEl)
      chatContainer.scrollTop = chatContainer.scrollHeight
    }
  }

  private escapeHtml(text: string): string {
    const div = document.createElement('div')
    div.textContent = text
    return div.innerHTML
  }
}