import * as THREE from 'three'
import { World } from './core/World'
import { Player } from './player/Player'
import { RemotePlayer } from './player/RemotePlayer'
import { NetworkManager, NetworkPlayerData } from './network/NetworkManager'
import { BlockType } from './blocks/Block'

// 
class Game {
  private scene: THREE.Scene
  private camera: THREE.PerspectiveCamera
  private renderer: THREE.WebGLRenderer
  private world: World
  private player: Player
  private network: NetworkManager
  private remotePlayers: Map<string, RemotePlayer> = new Map()
  private lastTime: number = 0
  private frameCount: number = 0
  private lastFpsTime: number = 0
  private lastNetworkUpdate: number = 0
  private networkUpdateInterval: number = 50 // 50ms

  constructor() {
    // 
    this.scene = new THREE.Scene()
    this.scene.background = new THREE.Color(0x87CEEB)
    this.scene.fog = new THREE.Fog(0x87CEEB, 20, 100)

    // 
    this.camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    )

    // 
    this.renderer = new THREE.WebGLRenderer({ antialias: true })
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.renderer.setPixelRatio(window.devicePixelRatio)
    this.renderer.shadowMap.enabled = true
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap
    document.getElementById('game-container')!.appendChild(this.renderer.domElement)

    // 
    this.setupLighting()

    // 
    this.world = new World(this.scene)

    // 
    this.player = new Player(this.camera, this.world)
    this.player.setPosition(0, 20, 0)

    // 
    this.network = new NetworkManager()
    this.setupNetworkCallbacks()

    // 
    this.player.setNetworkManager(this.network)

    // 
    window.addEventListener('resize', () => this.onWindowResize())

    // 
    this.animate()
  }

  private setupLighting(): void {
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.6)
    this.scene.add(ambientLight)

    const sunLight = new THREE.DirectionalLight(0xffffff, 0.8)
    sunLight.position.set(50, 100, 50)
    sunLight.castShadow = true
    sunLight.shadow.mapSize.width = 2048
    sunLight.shadow.mapSize.height = 2048
    sunLight.shadow.camera.near = 0.5
    sunLight.shadow.camera.far = 200
    sunLight.shadow.camera.left = -50
    sunLight.shadow.camera.right = 50
    sunLight.shadow.camera.top = 50
    sunLight.shadow.camera.bottom = -50
    this.scene.add(sunLight)
  }

  private setupNetworkCallbacks(): void {
    // 
    this.network.onPlayerJoin = (data: NetworkPlayerData) => {
      if (data.id !== this.network.getPlayerId()) {
        console.log(`: ${data.name}`)
        const remotePlayer = new RemotePlayer(this.scene, data)
        this.remotePlayers.set(data.id, remotePlayer)
        this.updatePlayerCount()
      }
    }

    // 
    this.network.onPlayerLeave = (playerId: string) => {
      const player = this.remotePlayers.get(playerId)
      if (player) {
        player.destroy()
        this.remotePlayers.delete(playerId)
        this.updatePlayerCount()
      }
    }

    // 
    this.network.onPlayerMove = (playerId: string, position) => {
      const player = this.remotePlayers.get(playerId)
      if (player) {
        player.updatePosition(position)
      }
    }

    // 
    this.network.onPlayerRotate = (playerId: string, rotation) => {
      const player = this.remotePlayers.get(playerId)
      if (player) {
        player.updateRotation(rotation)
      }
    }

    // 
    this.network.onBlockBreak = (x, y, z, playerId) => {
      if (playerId !== this.network.getPlayerId()) {
        this.world.setBlock(x, y, z, BlockType.AIR)
      }
    }

    // 
    this.network.onBlockPlace = (x, y, z, blockType, playerId) => {
      if (playerId !== this.network.getPlayerId()) {
        this.world.setBlock(x, y, z, blockType)
      }
    }

    // 
    this.network.onWorldSync = (blocks) => {
      console.log(` ${blocks.length} `)
      for (const block of blocks) {
        this.world.setBlock(block.x, block.y, block.z, block.type)
      }
    }

    // 
    this.network.onConnect = () => {
      this.updateConnectionStatus(true)
    }

    // 
    this.network.onDisconnect = () => {
      this.updateConnectionStatus(false)
      // 
      for (const player of this.remotePlayers.values()) {
        player.destroy()
      }
      this.remotePlayers.clear()
      this.updatePlayerCount()
    }

    // 
    const wsUrl = this.getWebSocketUrl()
    console.log(`: ${wsUrl}`)
    this.network.connect(wsUrl)
  }

  private getWebSocketUrl(): string {
    //  ws  wss
    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:'
    const host = window.location.host
    //  localhost:8080
    if (host.includes('localhost') || host.includes('127.0.0.1')) {
      return 'ws://localhost:8080'
    }
    return `${protocol}//${host}`
  }

  private updateConnectionStatus(connected: boolean): void {
    const statusEl = document.getElementById('connection-status')
    if (statusEl) {
      statusEl.textContent = connected ? '' : ''
      statusEl.className = connected ? 'connected' : 'disconnected'
    }
  }

  private updatePlayerCount(): void {
    const countEl = document.getElementById('player-count')
    if (countEl) {
      const count = this.remotePlayers.size + 1 // +1 
      countEl.textContent = `: ${count}`
    }
  }

  private onWindowResize(): void {
    this.camera.aspect = window.innerWidth / window.innerHeight
    this.camera.updateProjectionMatrix()
    this.renderer.setSize(window.innerWidth, window.innerHeight)
  }

  private animate(): void {
    requestAnimationFrame(() => this.animate())

    const currentTime = performance.now()
    const deltaTime = (currentTime - this.lastTime) / 1000
    this.lastTime = currentTime

    // 
    this.player.update(deltaTime)

    // 
    for (const player of this.remotePlayers.values()) {
      player.update(deltaTime, this.camera)
    }

    // 
    if (currentTime - this.lastNetworkUpdate > this.networkUpdateInterval) {
      this.syncToNetwork()
      this.lastNetworkUpdate = currentTime
    }

    // 
    this.world.update(this.player.getPosition())

    // 
    this.renderer.render(this.scene, this.camera)

    // UI
    this.updateUI(currentTime)
  }

  private syncToNetwork(): void {
    if (!this.network.isOnline()) return

    const pos = this.player.getPosition()
    this.network.sendPlayerMove({ x: pos.x, y: pos.y, z: pos.z })

    // 
    const rotation = this.player.getRotation?.()
    if (rotation) {
      this.network.sendPlayerRotate(rotation)
    }
  }

  private updateUI(currentTime: number): void {
    // 
    const pos = this.player.getPosition()
    document.getElementById('coords')!.textContent = 
      `${Math.floor(pos.x)}, ${Math.floor(pos.y)}, ${Math.floor(pos.z)}`

    // 
    document.getElementById('block-count')!.textContent = 
      this.world.getBlockCount().toString()

    // FPS
    this.frameCount++
    if (currentTime - this.lastFpsTime >= 1000) {
      document.getElementById('fps')!.textContent = this.frameCount.toString()
      this.frameCount = 0
      this.lastFpsTime = currentTime
    }
  }
}

