import * as THREE from 'three'
import { World } from '../core/World'
import { BlockType, getSelectedBlockType, initBlockSelector } from '../blocks/Block'
import { NetworkManager } from '../network/NetworkManager'

export class Player {
  private camera: THREE.PerspectiveCamera
  private world: World
  private network: NetworkManager | null = null
  private position: THREE.Vector3
  private velocity: THREE.Vector3
  private direction: THREE.Vector3
  private keys: Map<string, boolean> = new Map()
  private isLocked: boolean = false
  private pitch: number = 0
  private yaw: number = 0
  private onGround: boolean = false
  private raycaster: THREE.Raycaster

  // 
  private readonly MOVEMENT_SPEED = 8.0
  private readonly JUMP_FORCE = 10.0
  private readonly GRAVITY = 25.0
  private readonly PLAYER_HEIGHT = 1.8
  private readonly PLAYER_RADIUS = 0.3

  constructor(camera: THREE.PerspectiveCamera, world: World) {
    this.camera = camera
    this.world = world
    this.position = new THREE.Vector3(0, 20, 0)
    this.velocity = new THREE.Vector3(0, 0, 0)
    this.direction = new THREE.Vector3(0, 0, -1)
    this.raycaster = new THREE.Raycaster()

    this.setupControls()
    initBlockSelector()
  }

  setNetworkManager(network: NetworkManager): void {
    this.network = network
  }

  private setupControls(): void {
    document.addEventListener('keydown', (e) => this.keys.set(e.code, true))
    document.addEventListener('keyup', (e) => this.keys.set(e.code, false))

    document.addEventListener('click', () => {
      if (!this.isLocked) {
        document.body.requestPointerLock()
      }
    })

    document.addEventListener('pointerlockchange', () => {
      this.isLocked = document.pointerLockElement === document.body
    })

    document.addEventListener('mousemove', (e) => {
      if (this.isLocked) {
        this.yaw -= e.movementX * 0.002
        this.pitch -= e.movementY * 0.002
        this.pitch = Math.max(-Math.PI / 2 + 0.1, Math.min(Math.PI / 2 - 0.1, this.pitch))
        this.updateCameraRotation()
      }
    })

    document.addEventListener('mousedown', (e) => {
      if (this.isLocked) {
        if (e.button === 0) {
          this.breakBlock()
        } else if (e.button === 2) {
          this.placeBlock()
        }
      }
    })

    document.addEventListener('contextmenu', (e) => e.preventDefault())
  }

  private updateCameraRotation(): void {
    const euler = new THREE.Euler(this.pitch, this.yaw, 0, 'YXZ')
    this.camera.quaternion.setFromEuler(euler)
    this.direction.set(0, 0, -1).applyQuaternion(this.camera.quaternion)
  }

  private breakBlock(): void {
    const hit = this.raycast()
    if (hit) {
      this.world.setBlock(hit.x, hit.y, hit.z, BlockType.AIR)
      // 
      this.network?.sendBlockBreak(hit.x, hit.y, hit.z)
    }
  }

  private placeBlock(): void {
    const hit = this.raycast()
    if (hit) {
      const nx = hit.x + hit.normal.x
      const ny = hit.y + hit.normal.y
      const nz = hit.z + hit.normal.z
      
      if (!this.intersectsPlayer(nx, ny, nz)) {
        const blockType = getSelectedBlockType()
        this.world.setBlock(nx, ny, nz, blockType)
        // 
        this.network?.sendBlockPlace(nx, ny, nz, blockType)
      }
    }
  }

  private intersectsPlayer(bx: number, by: number, bz: number): boolean {
    const px = this.position.x
    const py = this.position.y - this.PLAYER_HEIGHT / 2
    const pz = this.position.z
    
    return (
      bx >= Math.floor(px - this.PLAYER_RADIUS) &&
      bx <= Math.floor(px + this.PLAYER_RADIUS) &&
      by >= Math.floor(py) &&
      by <= Math.floor(py + this.PLAYER_HEIGHT) &&
      bz >= Math.floor(pz - this.PLAYER_RADIUS) &&
      bz <= Math.floor(pz + this.PLAYER_RADIUS)
    )
  }

  private raycast(): { x: number, y: number, z: number, normal: THREE.Vector3 } | null {
    this.raycaster.setFromCamera(new THREE.Vector2(0, 0), this.camera)
    
    const reach = 5
    const origin = this.camera.position
    const dir = this.direction.clone().normalize()
    
    const step = 0.05
    for (let d = 0; d < reach; d += step) {
      const x = Math.floor(origin.x + dir.x * d)
      const y = Math.floor(origin.y + dir.y * d)
      const z = Math.floor(origin.z + dir.z * d)
      
      const block = this.world.getBlock(x, y, z)
      if (block !== BlockType.AIR) {
        const prevX = origin.x + dir.x * (d - step)
        const prevY = origin.y + dir.y * (d - step)
        const prevZ = origin.z + dir.z * (d - step)
        
        const normal = new THREE.Vector3()
        if (Math.floor(prevX) !== x) normal.x = Math.sign(prevX - x)
        else if (Math.floor(prevY) !== y) normal.y = Math.sign(prevY - y)
        else if (Math.floor(prevZ) !== z) normal.z = Math.sign(prevZ - z)
        
        return { x, y, z, normal }
      }
    }
    
    return null
  }

  update(deltaTime: number): void {
    const moveSpeed = this.MOVEMENT_SPEED
    const forward = new THREE.Vector3(Math.sin(this.yaw), 0, Math.cos(this.yaw))
    const right = new THREE.Vector3(Math.cos(this.yaw), 0, -Math.sin(this.yaw))
    
    let moveX = 0
    let moveZ = 0
    
    if (this.keys.get('KeyW')) moveZ -= 1
    if (this.keys.get('KeyS')) moveZ += 1
    if (this.keys.get('KeyA')) moveX -= 1
    if (this.keys.get('KeyD')) moveX += 1
    
    const moveDir = new THREE.Vector3()
      .addScaledVector(forward, -moveZ)
      .addScaledVector(right, moveX)
    
    if (moveDir.length() > 0) {
      moveDir.normalize()
      this.velocity.x = moveDir.x * moveSpeed
      this.velocity.z = moveDir.z * moveSpeed
    } else {
      this.velocity.x *= 0.8
      this.velocity.z *= 0.8
    }
    
    if (this.keys.get('Space') && this.onGround) {
      this.velocity.y = this.JUMP_FORCE
      this.onGround = false
    }
    
    this.velocity.y -= this.GRAVITY * deltaTime
    
    this.applyPhysics(deltaTime)
    this.camera.position.copy(this.position)
  }

  private applyPhysics(deltaTime: number): void {
    const newPos = this.position.clone()
    
    newPos.x += this.velocity.x * deltaTime
    if (this.checkCollision(newPos)) {
      newPos.x = this.position.x
      this.velocity.x = 0
    }
    
    newPos.z += this.velocity.z * deltaTime
    if (this.checkCollision(newPos)) {
      newPos.z = this.position.z
      this.velocity.z = 0
    }
    
    newPos.y += this.velocity.y * deltaTime
    if (this.checkCollision(newPos)) {
      if (this.velocity.y < 0) {
        this.onGround = true
      }
      newPos.y = this.position.y
      this.velocity.y = 0
    } else {
      this.onGround = false
    }
    
    this.position.copy(newPos)
    
    if (this.position.y < -10) {
      this.position.set(0, 20, 0)
      this.velocity.set(0, 0, 0)
    }
  }

  private checkCollision(pos: THREE.Vector3): boolean {
    const minX = Math.floor(pos.x - this.PLAYER_RADIUS)
    const maxX = Math.floor(pos.x + this.PLAYER_RADIUS)
    const minY = Math.floor(pos.y - this.PLAYER_HEIGHT)
    const maxY = Math.floor(pos.y)
    const minZ = Math.floor(pos.z - this.PLAYER_RADIUS)
    const maxZ = Math.floor(pos.z + this.PLAYER_RADIUS)
    
    for (let x = minX; x <= maxX; x++) {
      for (let y = minY; y <= maxY; y++) {
        for (let z = minZ; z <= maxZ; z++) {
          const block = this.world.getBlock(x, y, z)
          if (block !== BlockType.AIR) {
            return true
          }
        }
      }
    }
    return false
  }

  setPosition(x: number, y: number, z: number): void {
    this.position.set(x, y, z)
    this.camera.position.copy(this.position)
  }

  getPosition(): THREE.Vector3 {
    return this.position.clone()
  }

  getRotation(): { pitch: number; yaw: number } {
    return { pitch: this.pitch, yaw: this.yaw }
  }
}