// 
export enum BlockType {
  AIR = 0,
  GRASS = 1,
  DIRT = 2,
  STONE = 3,
  WOOD = 4,
  LEAVES = 5
}

// 
export interface BlockProperties {
  name: string
  color: number
  solid: boolean
}

// 
export const BLOCK_PROPERTIES: Record<BlockType, BlockProperties> = {
  [BlockType.AIR]: {
    name: '',
    color: 0x000000,
    solid: false
  },
  [BlockType.GRASS]: {
    name: '',
    color: 0x4a8522,
    solid: true
  },
  [BlockType.DIRT]: {
    name: '',
    color: 0x8b4513,
    solid: true
  },
  [BlockType.STONE]: {
    name: '',
    color: 0x808080,
    solid: true
  },
  [BlockType.WOOD]: {
    name: '',
    color: 0x654321,
    solid: true
  },
  [BlockType.LEAVES]: {
    name: '',
    color: 0x228b22,
    solid: true
  }
}

// 
let selectedBlockType: BlockType = BlockType.GRASS

export function getSelectedBlockType(): BlockType {
  return selectedBlockType
}

export function setSelectedBlockType(type: BlockType): void {
  selectedBlockType = type
}

// UI
export function initBlockSelector(): void {
  const buttons = document.querySelectorAll('.block-btn')
  buttons.forEach(btn => {
    btn.addEventListener('click', () => {
      buttons.forEach(b => b.classList.remove('active'))
      btn.classList.add('active')
      const blockType = (btn as HTMLElement).dataset.block
      switch (blockType) {
        case 'grass': setSelectedBlockType(BlockType.GRASS); break
        case 'dirt': setSelectedBlockType(BlockType.DIRT); break
        case 'stone': setSelectedBlockType(BlockType.STONE); break
        case 'wood': setSelectedBlockType(BlockType.WOOD); break
        case 'leaves': setSelectedBlockType(BlockType.LEAVES); break
      }
    })
  })
}